.flip {
  position: absolute;
  /* max-width: 500px;
  max-height: 500px; */
  width: auto;
  height: auto;
  cursor: pointer;
  will-change: transform, opacity;
  overflow-x: hidden;
  overflow-y: auto;
  display: flex;
}

a {
  color: #000000;
  /* font-size: 10px; */
}

a:hover {
  text-decoration: none;
  color: #000000;
}

.alt-link {
  font-size: 12px;
}

.alt-link:hover {
  color: #8b8b8b;

}

